* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url("../img/world-map.jpg");
  color: #212121;
  line-height: 1.5;

}

#search-box {
  font: inherit;
  font-size: 24px;
  font-weight: 500;
  padding: 10px;
  max-width: 320px;
  margin-bottom: 24px;
  margin-left: 40px;
  margin-top: 20px;
  border: none;
  border-radius: 10px;
  box-shadow: 0 5px 10px rgba(154, 160, 185, 0.05), 0 15px 40px rgba(0, 0, 0, 0.1);
}
#search-box:focus {
  outline: none;
}

.hero__title{
  font-size: 36px;
  color: aliceblue;
  margin-left: 40px;
}

.country-list {
  list-style: none;
  color: aliceblue;
}

.country-list li {
  display: flex;
  padding-bottom: 10px;
  font-size: 22px;
  font-weight: 500;
}

.country-list img {
  margin-right: 10px;
}

.country-info {
  margin-left: 50px;
}

.country-info img {
  margin-right: 10px;
  color: aliceblue;

}

.country-info h1 {
  display: flex;
  align-items: center;
  margin-top: 0;
  font-size: 40px;
  color: aliceblue;

}

.country-info p {
  font-size: 22px;
  font-weight: 500;
  color: aliceblue;

}